import React, { useState } from "react";

import {
  Drawer,
  Divider,
  Toolbar,
  Chip,
  Typography,
  InputBase,
  Avatar,
} from "@material-ui/core";
import { Search as SearchIcon } from "@material-ui/icons";

import { getAvatar } from '../index'
import { useStyles } from "./style";

interface Props {
  paperNumber: number;
  VISTags: Record<string, boolean>;
  MLTags: Record<string, boolean>;
  onClickFilter: (k: string, type: 'VIS' | 'ML') => void;
}

export function SideBar(props: Props) {
  const { paperNumber, VISTags, MLTags, onClickFilter } = props;
  const classes = useStyles();

  return (
    <Drawer
      className={classes.drawer}
      variant="permanent"
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.drawerContainer}>
        <Toolbar />

        <Typography variant="subtitle2" className={classes.filterTitle}>
          Surveyed papers:
        </Typography>
        <Typography variant="h4" className={classes.paperNumber}>
          {paperNumber}
        </Typography>

        <Divider />

        <Typography variant="subtitle2" className={classes.filterTitle}>
          Keywords search:
        </Typography>
        <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
          <InputBase
            placeholder="Search"
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            inputProps={{ "aria-label": "search" }}
          />
        </div>

        <Divider />

        <Typography variant="subtitle2" className={classes.filterTitle}>
          VIS process filter:
        </Typography>
        <div className={classes.filters}>
          {Object.entries(VISTags).map(([v, checked]) => (
            <Chip
              key={v}
              avatar={<Avatar>{getAvatar(v)}</Avatar>}
              label={v}
              clickable
              variant={checked ? "default" : "outlined"}
              color="primary"
              onClick={() => onClickFilter(v, 'VIS')}
            />
          ))}
        </div>

        <Divider />

        <Typography variant="subtitle2" className={classes.filterTitle}>
          ML tasks filter:
        </Typography>
        <div className={classes.filters}>
          <div className={classes.filters}>
            {Object.entries(MLTags).map(([m, checked]) => (
              <Chip
                key={m}
                avatar={<Avatar>{getAvatar(m)}</Avatar>}
                label={`${m[0].toUpperCase()}${m.slice(1)}`}
                clickable
                variant={checked ? "default" : "outlined"}
                color="secondary"
                onClick={() => onClickFilter(m, 'ML')}
              />
            ))}
          </div>
        </div>
      </div>
    </Drawer>
  );
}
