import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";

import {
  Typography,
  CssBaseline,
  Paper,
  ThemeProvider,
} from "@material-ui/core";

import { TopBar } from "./TopBar";
import { Papers } from "./Papers";
import { SideBar } from "./SideBar";

import { useStyles, theme } from "./style";

export interface Paper {
  name: string;
  venue: string;
  year: number;
  VIS: string[];
  ML: string[];
}

export const getAvatar = (s: string) => {
  const pieces = s.split(" ");
  if (pieces.length > 1) {
    return `${pieces[0][0].toUpperCase()}${pieces[1][0].toUpperCase()}`;
  } else if(s === 'Clustering') {
      return 'CLT'
  } else if(s === 'Classification') {
      return 'CLS'
  } else if (s === 'Regression') {
      return 'RE'
  } else if (s === 'Reinforcement') {
      return 'RF'
  }
  return `${pieces[0][0].toUpperCase()}`;
};

export default function App() {
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [papers, setPapers] = useState<Paper[]>([]);
  const [VISTags, setVISTags] = useState({});
  const [MLTags, setMLTags] = useState({});
  const isMenuOpen = Boolean(anchorEl);
  const menuId = "primary-search-account-menu";

  useEffect(() => {
    const fetchData = async () => {
      const papers = await fetch("/assets/papers.json").then((res) =>
        res.json()
      );
      setPapers(papers);
      setVISTags(
        papers.reduce((o, d) => {
          d.VIS.forEach((v) => {
            if (!(v in o)) {
              o[v] = true;
            }
          });
          return o;
        }, {})
      );
      setMLTags(
        papers.reduce((o, d) => {
          d.ML.forEach((v) => {
            if (!(v in o)) {
              o[v] = true;
            }
          });
          return o;
        }, {})
      );
    };

    fetchData();
  }, []);

  const onProfileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const onClickFilter = (tag: string, type: "VIS" | "ML") => {
    if (type === "VIS") {
      setVISTags({
        ...VISTags,
        [tag]: !VISTags[tag],
      });
    } else if (type === "ML") {
      setMLTags({
        ...MLTags,
        [tag]: !MLTags[tag],
      });
    }
  };

  const papersAfterFilter = papers.filter(
    (p) => p.ML.every((m) => MLTags[m]) && p.VIS.every((v) => VISTags[v])
  );

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <CssBaseline />

        <TopBar menuId={menuId} onProfileMenuOpen={onProfileMenuOpen} />

        <SideBar
          paperNumber={papersAfterFilter.length}
          VISTags={VISTags}
          MLTags={MLTags}
          onClickFilter={onClickFilter}
        />

        <Papers papers={papersAfterFilter} />
      </div>
    </ThemeProvider>
  );
}

ReactDOM.render(<App />, document.getElementById("root"));
